<template>
  <div class="hot">
    <p class="top">
      <span>热搜榜</span>
    </p>
    <Loading v-if="isLoading"></Loading>
    <ul>
      <li
        @click="$emit('hotSearch', item.searchWord)"
        v-for="(item, index) in content"
        :key="index"
      >
        <span class="id">{{ index + 1 }}</span>
        <span class="content">{{ item.searchWord }}</span>
        <span class="icon">
          <img :src="item.iconUrl" alt="" />
        </span>
      </li>
    </ul>
  </div>
</template>
<style lang="less" scoped>
.hot {
  p.top {
    text-align: left;
    color: #4e4e4e;
    border-bottom: 1px solid rgba(100, 100, 100, 0.1);
    padding-bottom: 14px;
  }
  ul {
    width: 100%;
    //   background-color: blanchedalmond;
    overflow: hidden;
    li {
      width: 50%;
      height: 38px;
      //   background-color: blueviolet;
      float: left;
      line-height: 38px;
      text-align: left;
      margin: 4px 0;
      span.id {
        color: #5e5d5d;
        width: 30px;
        display: inline-block;
        text-align: left;
      }
      &:nth-child(-n + 3) {
        font-size: 1rem;
        font-weight: 600;
        & .id {
          color: red;
        }
      }
      &:nth-child(n + 4) {
        color: #797979;
      }
      .icon {
        height: 18px;
        display: inline-block;
        margin-left: 5px;
        transform: translateY(4px);
        img {
          height: 100%;
        }
      }
    }
  }
}
</style>
<script>
import Loading from "@/components/Loading";
export default {
  name: "HotSearch",
  data() {
    return {
      content: [],
      isLoading: true,
    };
  },
  components: {
    Loading,
  },
  created() {
    this.$axios
      .get("/search/hot/detail")
      .then((data) => {
        return data.data.data;
      })
      .then((item) => {
        this.content = item;
        // console.log(item);
        this.isLoading=false
      });
  },
};
</script>
