<template>
  <div class="box">
    <div class="playMent">
      <div @click="$emit('addMusic', dataNewSong)">
        <img src="../assets/images/icon/playMent.png" alt="" />
        <p>播放全部<span>(搜索结果)</span></p>
      </div>
      <!-- <span><img src="../../assets/images/icon/onMent.png" alt=""/></span> -->
    </div>
    <div class="list">
      <ul>
        <li
          v-for="(item, index) in data"
          @click="
            $emit(
              'play',
              {
                al: {
                  picUrl: item.album.artist.img1v1Url,
                },
                name: item.name,
                ar: [{ name: item.artists[0].name }],
                id: item.id,
              },
              (isActive = index)
            )
          "
          :key="index"
          :style="{ borderColor: isActive == index ? 'red' : '' }"
        >
          <span
            class="left"
            :style="{ color: isActive == index ? 'red' : '' }"
            >{{ index + 1 }}</span
          >
          <div class="content">
            <p>{{ item.name }}</p>
            <p>{{ item.artists[0].name }} - {{ item.album.name }}</p>
          </div>
          <div class="icon">
            <span></span>
          </div>
        </li>
      </ul>
    </div>
    <Loading v-if="isLoading"></Loading>
  </div>
</template>
<style lang="less" scoped>
.box {
  background-color: white;
  padding-bottom: 30px;
  margin-top: 10px;
  border-radius: 20px 20px 0 0;
}

.playMent {
  width: 94%;
  height: 48px;
  box-sizing: border-box;
  padding: 3%;
  display: flex;
  position: fixed;
  background-color: white;
  div {
    flex: 1;
    img {
      float: left;
      width: 26px;
      margin-right: 14px;
    }
    p {
      line-height: 26px;
      text-align: left;
      font-weight: 600;
      color: #333;
      span {
        font-size: 0.8rem;
        margin-left: 10px;
        color: #afafaf;
      }
    }
  }
  span {
    width: 20%;
    height: 100%;
    img {
      width: 20px;
      margin-top: 4px;
      margin-right: 10px;
      float: right;
    }
  }
}
.list {
  padding: 3%;
  box-sizing: border-box;
  ul {
    width: 100%;
    margin-top: 40px;
    li {
      height: 64px;
      color: #7a7a7a;
      border-bottom: 1px solid white;
      span.left {
        float: left;
        width: 10%;
        font-size: 1.2rem;
        line-height: 64px;
        height: 100%;
        font-weight: 600;
      }
      div.content {
        width: 78%;
        height: 100%;
        float: left;
        padding-left: 4px;
        p {
          height: 50%;
          text-align: left;
          vertical-align: bottom;
          overflow: hidden;
          font-size: 0.5rem;
          &:first-child {
            font-size: 0.9rem;
            line-height: 38px;
            color: #333;
          }
        }
      }
      div.icon {
        height: 100%;
        width: 10%;
        float: right;
        span {
          width: 100%;
          height: 32px;
          display: inline-block;
          margin-top: 16px;
          background-image: url(../assets/images/icon/detail.png);
          background-repeat: no-repeat;
          background-position: right;
        }
      }
    }
  }
}
</style>
<script>
import Loading from "@/components/Loading";

export default {
  name: "SearchDetail",
  components: { Loading },
  data() {
    return {
      data: [],
      //   playData: '11112'
      isActive: null,
      isLoading: true,
      itemData: {},
      dataNewSong:[]
    };
  },
  props: {
    searchData: {
      type: String,
      default: function() {
        return "";
      },
    },
  },
  computed: {},
  watch: {
    searchData: function() {
      this.data = [];
      this.isLoading = true;
      if (this.searchData != undefined) {
        this.$axios
          .get("/search?keywords=" + this.searchData)
          .then((data) => {
            return data.data.result.songs;
          })
          .then((item) => {
            this.data = item;
            this.isLoading = false;
          });
      }
    },
  },
  created() {
    this.data = [];
    this.isLoading = true;
    if (this.searchData != undefined) {
      this.$axios
        .get("/search?keywords=" + this.searchData)
        .then((data) => {
          return data.data.result.songs;
        })
        .then((item) => {
          this.data = item;
          this.isLoading = false;
          // console.log(444);?

          // console.log(item);
          let dataArr = [];
          for (let i = 0; i < this.data.length; i++) {
            dataArr.push({
              al: {
                picUrl: item[i].album.artist.img1v1Url,
              },
              name: item[i].name,
              ar: [{ name: item[i].artists[0].name }],
              id: item[i].id,
            });
          }
          this.dataNewSong = dataArr;
        });
    }
  },
  methods: {
    loadMusic() {},
  },
};
</script>
