<template>
  <div class="searchPage">
    <div class="input">
      <img
        @click="$router.go(-1)"
        src="../assets/images/icon/returnKey.png"
        alt=""
      />
      <input
        type="text"
        name=""
        id=""
        v-model="inputVal"
        placeholder="大家都在搜"
        @keyup.enter="submit"
        @input="inputVal == '' ? (isReturn = false) : (isReturn = true)"
        @click="inputVal != '' ? (isReturn = true) : ''"
      />
    </div>
    <div class="searchShade"></div>

    <div class="return" v-if="isReturn">
      <ul>
        <span
          >搜索:"{{ inputVal }}"<b
            @click="(isReturn = false), (isDetail = false)"
          ></b
        ></span>
        <div class="shade">1</div>

        <Loading v-if="isLoading"></Loading>
        <li
          v-for="(item, index) in content"
          :key="index"
          @click="
            SearchChange(item.name),
              (isReturn = false),
              (isDetail = true),
              submit()
          "
        >
          <p><i></i>{{ item.name }}</p>
        </li>
      </ul>
    </div>
    <div class="history" v-if="!isDetail">
      <span class="left">历史</span>
      <div>
        <p :style="{ width: pWidth + 'px' }">
          <span v-for="(item, index) in allHislist" :key="index" @click="inputVal=item,isReturn=true">{{
            item
          }}</span>
        </p>
      </div>
      <i @click="delSubmit">删除</i>
    </div>
    <SearchDetail @addMusic="$emit('addMusicList',$event)" @play='$emit("play",$event)' :searchData="searchVal" v-if="isDetail"></SearchDetail>
    <HotSearch
      @hotSearch="(inputVal = $event), submit($event),isReturn=true"
      @click="SearchChange(inputVal), (isReturn = true), (isDetail = true)"
      v-if="!isDetail"
    ></HotSearch>
  </div>
</template>

<style lang="less" scoped>
.searchPage {
  width: 100%;
  background-color: white;
  padding: 3%;
  box-sizing: border-box;
  position: relative;
  .searchShade {
    height: 30px;
    position: relative;
    z-index: -1;
  }
  .input {
    overflow: hidden;
    position: fixed;
    width: 94%;
    top: 0;
    background-color: white;
    padding: 20px 0 10px 0;
    img {
      float: left;
      height: 32px;
    }
    input {
      width: 84%;
      height: 32px;
      float: left;
      margin-left: 10px;
      box-sizing: border-box;
      text-align: left;
      border: none;
      border-bottom: 1px solid #333;
      outline: none;
      font-size: 1.2rem;
    }
  }
  .history {
    overflow: hidden;
    color: #4e4e4e;
    margin: 26px 0;
    span.left {
      float: left;
      height: 26px;
      font-weight: 600;
      margin: 2px 0;
      font-size: 0.9rem;
    }
    div {
      margin-left: 10px;
      float: left;
      width: 80%;
      overflow: hidden;
      overflow-x: auto;
      &::-webkit-scrollbar {
        display: none;
      }
      p {
        text-align: left;
      }
      span {
        display: inline-block;
        background-color: rgba(100, 100, 100, 0.1);
        margin-right: 10px;
        padding: 2px 8px;
        border-radius: 50px;
      }
    }
    i {
      width: 25px;
      height: 25px;
      text-indent: -9999px;
      display: block;
      float: right;
      background-repeat: no-repeat;
      background-size: cover;
      background-image: url(../assets/images/icon/del.png);
    }
  }
  .return {
    width: 82vw;
    height: 60vh;
    position: absolute;
    top: 50px;
    right: 3%;
    background-color: white;
    box-shadow: 0px 0px 18px #a1a1a1;
    z-index: 11;
    overflow: hidden;
    overflow-y: auto;
    ul {
      height: 200px;
      position: relative;
      .shade {
        padding: 12px 3%;
        background-color: yellow;
      }
      span {
        width: 82%;
        padding: 12px 3%;
        display: block;
        box-sizing: border-box;
        border-bottom: 1px solid rgb(233, 233, 233);
        color: rgb(69, 168, 226);
        background-color: white;
        text-align: left;
        position: fixed;
        b {
          width: 24px;
          height: 24px;
          background-image: url(../assets/images/icon/exit.png);
          float: right;
          background-size: cover;
        }
      }
      li {
        padding: 12px 0;
        border-bottom: 1px solid rgb(233, 233, 233);
        text-align: left;
        color: rgb(139, 138, 138);

        p {
          width: 92%;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          padding: 0 3%;
          text-align: left;
        }
        i {
          float: left;
          width: 24px;
          height: 24px;
          background-image: url(../assets/images/icon/search.png);
          background-size: cover;
          margin-right: 6px;
        }
      }
    }
  }
}
</style>

<script>
import HotSearch from "@/views/HotSearch";
import SearchDetail from "@/views/SearchDetail";
import Loading from "@/components/Loading";
export default {
  name: "SearchPage",
  components: { HotSearch, SearchDetail, Loading },
  data() {
    return {
      pWidth: 1000,
      content: {},
      inputVal: "",
      isReturn: false,
      isDetail: false,
      searchVal: "",
      allHislist: [],
      isLoading: true,
    };
  },

  methods: {
    SearchChange(value) {
      this.searchVal = value;
    },
    delSubmit() {
      localStorage.clear();
      this.allHislist = [];
    },
    submit() {
      //判断是否有重复

      if (this.inputVal != "") {
        var index = this.allHislist.findIndex((ele) => {
          return ele == this.inputVal;
        });
        //如果有的话就删除重复
        if (index != -1) {
          this.allHislist.splice(index, 1);
        }
        //向数组第一位添加
        this.allHislist.unshift(this.inputVal);
        //如果数组长度大于4 就删除最后一项
        if (this.allHislist.length > 11) {
          this.allHislist.splice(10, 1);
        }
        // 本地存储历史记录数组
        localStorage.allHislist = JSON.stringify(this.allHislist);
        // let span = document.querySelectorAll(".history>div>p>span");
        let spanWidth = 0;
        for (let i = 0; i < this.allHislist.length; i++) {
          // spanWidth += span[i].clientWidth + 20;
          spanWidth += 120;
        }
        this.pWidth = spanWidth;
      }
    },
  },
  watch: {
    inputVal: function() {
      // this.isReturn = true ;
    this.isLoading=true
      if (this.inputVal != "") {
        this.$axios
          .get("/search?keywords=" + this.inputVal)
          .then((data) => {
            return data.data.result.songs;
          })
          .then((item) => {
            this.content = item;
            this.isLoading=false;
          });
      }
    },
  },
  created() {
    let all = localStorage.allHislist;
    if (all) {
      // 将数组转字符串
      this.allHislist = JSON.parse(all);
    }
  },
};
</script>
